.modal {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
  
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .modal__container {
    position: relative;
    top: 10rem;
    background-color: #FECA64;
    padding: 1rem;
    width: 400px;
    font-weight: bold;
  }
  
  .modal__close-button {
    position: absolute;
    top: 0;
    right: 0;
    border: 0;
    background-color: #4C0100;
    color: white;
    font-size: 15px;
    font-weight: bold;
    padding: 0.5rem 1rem;
    border-radius: 5px;
    margin: 2px;
    cursor: pointer;
  }